.main-nav {
  position: relative;
  width: auto;
  margin: 0;
}

.main-nav__list {
  font-family: @font-family-base;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  display: none;
}

.main-nav__checkbox {
  display: none;
}

.main-nav__checkbox:checked ~ .main-nav__list {
  display: block;
}

.main-nav__opened {
  display: block;
}

.main-nav__opened::after {
  display: none;
}

.main-nav__closed {
  display: none;
}

.main-nav__close-button {
  // position: absolute;
  content: "";
  background-color: @bg-color-main-button;
  top: 17px;
  right: 10px;
  width: 22px;
  height: 22px;
  cursor: pointer;
  border: none;
}

.main-nav__close-button::after {
  content: "";
  position: absolute;
  top: 18px;
  right: 0;
  width: 22px;
  height: 3px;
  background-color: @white;
  // -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.main-nav__close-button::before {
  content: "";
  position: absolute;
  top: 18px;
  right: 0;
  width: 22px;
  height: 3px;
  background-color: @white;
  transform: rotate(45deg);
}
